import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
//import View from '@vkontakte/vkui/dist/components/View/View';
import { View, Panel, PanelHeader, Header, Group, Cell, Root, Input, Div, Button, Alert, Text, Headline } from '@vkontakte/vkui';

import Icon28MessageOutline from '@vkontakte/icons/dist/28/message_outline';
import '@vkontakte/vkui/dist/vkui.css';

class App extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            activeView: 'view1',
            popout: null,
            input1: '',
            input2: '',
            input3: '',
            input4: '',
            input5: '',
            input6: '',
            input7: '',
        };
        this.openPopout = this.openPopout.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.closePopout = this.closePopout.bind(this);
    }
    
    handleChange(event) {
        const target = event.target;
        this.setState({ [target.name]: target.value });
    }
    
    
//    
//    capitalize = function(str1){
//  return str1.charAt(0).toUpperCase() + str1.slice(1);
//}
//    
    
    
    
// console.log(capitalize('js string exercises'));
    openPopout() {
        var counter = 0;
        var wrong = ['Неправильные ответы:', <br />];
        var answ1 = 'Байкал';
        var answ2 = 'Лиса';
        var answ3 = 'Эверест';
        var answ4 = 'Юпитер';
        var answ5 = 'Сова';
        var answ6 = 'Билл Гейтс';
        var answ7 = 'Лена';
        
        
        if (this.state.input1.trim().toLowerCase() === answ1.toLowerCase()) { counter++; } 
        else { wrong = [...wrong, (this.state.input1 == '' ? '[Пусто]' : this.state.input1) + ' - ' + answ1, <br />]; }
        
        if (this.state.input2.trim().toLowerCase() === answ2.toLowerCase()) { counter++; }  
        else { wrong = [...wrong, (this.state.input2 == '' ? '[Пусто]' : this.state.input2) + ' - ' + answ2, <br />]; }
        
        if (this.state.input3.trim().toLowerCase() === answ3.toLowerCase()) { counter++; }  
        else { wrong = [...wrong, (this.state.input3 == '' ? '[Пусто]' : this.state.input3) + ' - ' + answ3, <br />]; }
        
        if (this.state.input4.trim().toLowerCase() === answ4.toLowerCase()) { counter++; }  
        else { wrong = [...wrong, (this.state.input4 == '' ? '[Пусто]' : this.state.input4) + ' - ' + answ4, <br />]; }
        
        if (this.state.input5.trim().toLowerCase() === answ5.toLowerCase()) { counter++; }  
        else { wrong = [...wrong, (this.state.input5 == '' ? '[Пусто]' : this.state.input5) + ' - ' + answ5, <br />]; }
        
        if (this.state.input6.trim().toLowerCase() === answ6.toLowerCase()) { counter++; }  
        else { wrong = [...wrong, (this.state.input6 == '' ? '[Пусто]' : this.state.input6) + ' - ' + answ6, <br />]; }
        
        if (this.state.input7.trim().toLowerCase() === answ7.toLowerCase()) { counter++; }  
        else { wrong = [...wrong, (this.state.input7 == '' ? '[Пусто]' : this.state.input7) + ' - ' + answ7, <br />]; }
        if (counter == 7) wrong = []
        this.setState({ 
            popout: <Alert 
                actions={[{title: 'Отлично', autoclose: true, mode: 'cancel'}]}
                onClose={this.closePopout}>
                
                <h2>Угадайка!</h2>
                <p>Количество вопросов, на которые вы ответили правильно: {counter} из 7</p>
                <p>{wrong}</p>
            </Alert>
            })
    }
    
    closePopout() {
        this.setState({popout: null})
    }
    
    
    render() {
        return (
          <Root activeView={this.state.activeView}>
            <View popout={this.state.popout} activePanel="panel-1-1" id="view1">
              <Panel id="panel-1-1">
                <PanelHeader>Игра "Угадайка!"</PanelHeader>
                <Group>
                <Div>Назовите самое глубокое озеро в мире</Div>
              <Input name='input1' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <Div>Какое животное имеет рыжий окрас зимой?</Div>
              <Input name='input2' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <Div>Назовите самую высокую вершину мира</Div>
              <Input name='input3' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <Div>Назовите пятую планету от Солнца</Div>
              <Input name='input4' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <Div>Какая хищная птица охотится по ночам?</Div>
              <Input name='input5' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <Div>Кто основал Microsoft?</Div>
              <Input name='input6' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <Div>Как называется самая длинная река в <b>России</b>?</Div>
              <Input name='input7' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Cell></Cell>
                 <Div>
                   <Button size="xl" mode="primary" onClick={this.openPopout}>Отправить ответы</Button>
                 </Div>
              </Panel>
            </View>
          </Root> 
        )
    }
}



export default App;

